.detail-page {
  width: 100%;
  height: 100%; }
  .detail-page .detail-content {
    padding: 0 4vw; }
  .detail-page .content-image, .detail-page .content-info {
    display: inline-block;
    vertical-align: top; }
  .detail-page .content-image {
    max-width: 40%;
    overflow: hidden; }
    .detail-page .content-image img {
      max-width: 100%; }
  .detail-page .content-info {
    width: 60%; }



