.App {
  text-align: left;
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  min-height: 100%; }
  .App .chrome-language-hack {
    font-size: 1px;
    height: 0px;
    opacity: 0; }
  .App .left-nav-container, .App .hover-drawer-container {
    display: inline-block;
    position: absolute; }
  .App .hover-drawer-container {
    width: 60%;
    right: 0;
    top: 0;
    bottom: 0; }
  .App .main-header-container {
    z-index: 2;
    position: relative;
    background-color: white;
    width: 100%; }
  .App .left-nav-container {
    width: 340px;
    height: 100%;
    z-index: 2; }
  .App .hover-drawer-container {
    z-index: 1;
    height: 100%; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



