.hover-drawers {
  width: 385px;
  height: 100px; }
  .hover-drawers .hover-drawer-open, .hover-drawers .hover-drawer-close {
    -webkit-transition: -webkit-transform 0.6s ease;
    transition: -webkit-transform 0.6s ease;
    -o-transition: transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
    position: absolute;
    margin: auto;
    display: block;
    bottom: 0px;
    top: 10vh;
    right: 5vw;
    width: 100%;
    padding: 30px;
    text-align: center;
    max-height: 100%; }
    .hover-drawers .hover-drawer-open .hover-photo-helper, .hover-drawers .hover-drawer-close .hover-photo-helper {
      display: inline-block;
      height: 100%;
      vertical-align: middle; }
    .hover-drawers .hover-drawer-open .hover-photo, .hover-drawers .hover-drawer-close .hover-photo {
      max-width: 100%;
      max-height: 80%;
      vertical-align: middle; }
  .hover-drawers .hover-drawer-open {
    z-index: 2;
    -webkit-transform: translateX(0%);
        -ms-transform: translateX(0%);
            transform: translateX(0%); }
  .hover-drawers .hover-drawer-close {
    -webkit-transform: translateX(-120%);
        -ms-transform: translateX(-120%);
            transform: translateX(-120%); }
  .hover-drawers .background-layer {
    background-color: #FFF;
    width: 100%;
    padding: 0; }



